import { ref, reactive } from "vue";

export default function createPDF() {

	const map = new Map();

	function createMap(_function) {
		let newMap = new Map();

		_function(newMap);

		return newMap;
	}

	map
		.set({ id: 'element-1', name: "tags", ru: "", type: "default" }, [
			// { name: "length", ru: "", type: "number", value: 225 }
		])
		.set({ id: 'element-2', name: "category", ru: "", type: "graph" }, [
			{
				name: "length", ru: "", type: "number", min: 3, max: 10, value: 10
			},
			{
				name: "number", ru: "", type: "checkbox", value: true
			},
			{
				name: "percent", ru: "", type: "checkbox", value: false
			},
		])
		.set({ id: 'element-3', name: "sentiments", ru: " ", type: "graph" }, [
			{
				name: "number", ru: "", type: "checkbox", value: true
			},
			{
				name: "percent", ru: "", type: "checkbox", value: false
			},
		])
		.set({ id: 'element-4', name: "resources", ru: "", type: "graph" }, [
			{ name: "length", ru: "", type: "number", min: 3, max: 10, value: 10 },
			{ name: "number", ru: "", type: "checkbox", value: true },
			{ name: "percent", ru: "", type: "checkbox", value: false },
		])
		.set({ id: 'element-5', name: "dates", ru: "", type: "graph" }, [
			{
				name: "number", ru: "", type: "checkbox", value: true
			},
			{
				name: "percent", ru: "", type: "checkbox", value: false
			},
		])
		.set({ id: 'element-6', name: "smi_table", ru: "", type: "table" }, [
			{
				name: "columns", ru: "", type: "columns", value: [
					{ name: "number", ru: "", selected: "hide" },
					{ name: "title", ru: "", selected: true },
					{ name: "content", ru: "", selected: "default" },
					{ name: "date", ru: "", selected: true },
					{ name: "resource", ru: "", selected: true },
					{ name: "resource_link", ru: "", selected: true },
					{ name: "news_link", ru: "URL", selected: true },
					{ name: "sentiment", ru: "", selected: true },
					{ name: "category", ru: "", selected: true },
				]
			},
			{
				name: "tag_highlight", ru: " ", type: "dropdown", value: [
					{ name: "bold", ru: "", type: "checkbox", value: true },
					{ name: "tag_color", ru: " ", type: "color", value: "#000000", show: false },
					{ name: "back_color", ru: " ", type: "color", value: "#dcdcdc", show: false },
				],
			},
			{
				name: "order", ru: "", type: "dropdown", value: [...createMap(newMap => {
					newMap.set({ name: "date", ru: "", selected: false, type: "select", outputIndex: true }, [
						{ name: " ", index: 0, selected: true },
						{ name: " ", index: 1, selected: false }
					])
				})], draggables: [...createMap(newMap => {
					newMap.set({ name: "sentiments", ru: "", type: "draggable", showResources: true, selected: true }, [
						{ name: "positive", ru: "", selected: true, defaultPosition: 0 },
						{ name: "neutral", ru: "", selected: true, defaultPosition: 1 },
						{ name: "negative", ru: "", selected: true, defaultPosition: 2 },
					])
					newMap.set({ name: "categories", ru: "", type: "draggable", showResources: false, selected: false }, [
						{ name: " ", selected: true },
						{ name: " ", selected: true },
						{ name: " ", selected: true },
						{ name: " ", selected: true },
						{ name: "", selected: true },
						{ name: " ", selected: true },
						{ name: " ", selected: true },
						{ name: "LiveJournal", selected: true },
						{ name: " ", selected: true },
						{ name: "", selected: true },
						{ name: "", selected: true },
						{ name: " ", selected: true },
					])
				})]
			},
			{ name: "text_length", ru: " ", selected: true, min: 70, max: 500, type: "number", value: 400 },
		])
		.set({ id: 'element-7', name: "soc_table", ru: " ", type: "table" }, [
			{
				name: "columns", ru: "", type: "columns", value: [
					{ name: "number", ru: "", selected: "hide" },
					{ name: "content", ru: " ", selected: "default" },
					{ name: "resource", ru: "", selected: true },
					{ name: "date", ru: "", selected: true },
					{ name: "category", ru: "", selected: true },
					{ name: "resource_link", ru: "", selected: true },
					{ name: "news_link", ru: "URL", selected: true },
					{ name: "sentiment", ru: " ", selected: true },
				]
			},
			{
				name: "tag_highlight", ru: " ", type: "dropdown", value: [
					{ name: "bold", ru: "", type: "checkbox", value: true },
					{ name: "tag_color", ru: " ", type: "color", value: "#000000", show: false },
					{ name: "back_color", ru: " ", type: "color", value: "#dcdcdc", show: false },
				],
			},
			{
				name: "order", ru: "", type: "dropdown", value: [...createMap(newMap => {
					newMap.set({ name: "date", ru: "", selected: false, type: "select", outputIndex: true }, [
						{ name: " ", index: 0, selected: true },
						{ name: " ", index: 1, selected: false }
					])
				})], draggables: [...createMap(newMap => {
					newMap.set({ name: "sentiments", ru: "", type: "draggable", selected: true }, [
						{ name: "positive", ru: "", selected: true, defaultPosition: 0 },
						{ name: "neutral", ru: "", selected: true, defaultPosition: 1 },
						{ name: "negative", ru: "", selected: true, defaultPosition: 2 },
					])
					newMap.set({ name: "categories", ru: "", type: "draggable", selected: false }, [
						{ name: "", selected: true },
						{ name: "Facebook", selected: true },
						{ name: "Twitter", selected: true },
						{ name: "Instagram", selected: true },
						{ name: "LinkedIn", selected: true },
						{ name: "Youtube", selected: true },
						{ name: "", selected: true },
						{ name: "_", selected: true },
						{ name: "Telegram", selected: true },
						{ name: "TikTok", selected: true },
					])
				})]
			},
			{ name: "text_length", ru: " ", selected: true, min: 70, max: 500, type: "number", value: 400 },
		])

	return reactive(ref(Object.entries([...map.entries()]).map(el => el[1])));
}